@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Pacifico&family=Patua+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Pacifico&family=Patua+One&family=Tiro+Devanagari+Sanskrit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Pacifico&family=Patua+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Pacifico&family=Patua+One&family=Tiro+Devanagari+Sanskrit&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*
{
  border: 0;
  padding: 0;
  margin: 0;
}

.wrapper
{
  background-color: #0423F6;
}

.navBar
{
  background-color: transparent;
  width: 100%;
  position: fixed;
  background-color: #0423F6;
}
.bttn_list
{
  
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: flex-end;
  padding-top: 10px;
  color: white;
}

.mobile_icon
{
  display: none;
}

.mobile_menu
{
  display: none;
}
.nav_bttn
{
  color: white;
  font-size: large;
  padding-right: 10%;
}

.nav_bttn:hover
{
  border-bottom: 5px solid white;
  cursor: pointer;
}


.title
{
  font-family: 'Pacifico', cursive;
  color:white;
  float: left;
  padding-left: 50px;
}

.def_wrapper
{
  background: #0423F6;
  padding-bottom: 100px;
}

.def_contain
{
  width: 100%;
  align-items: center;
  display:flex;
  flex-direction: column;
  padding-top: 120px;
}
.name_div
{
  width: 50%;
}
.name
{
  font-size:100px;
  color: white;
}
.noun
{
  font-size: 20px;
  color: white;
  text-align: left;
  width: 50%;
  
}

.defn_div
{
  padding-top: 40px;
  width: 50%;
  padding-bottom: 40px;
}

.definition{
  color: white;
  font-size: larger;
  text-align: center;
}

.button
{
  background-color: #0019C5;
  color: white;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 60px;
  padding-right: 60px;
  font-size: large;
  transition: ease-out 0.5s;
}

.button:hover
{
  box-shadow: inset 400px 0 0 0 #0501B4;;
}

.socials_list
{
  list-style: none;

}

.social{
  display: inline;
  padding-right: 15px;
}


.social_logo
{
  cursor: pointer;
  color: white;
}

@media (max-width:550px)
{
  .name{
    font-size: 50px;
  }

  .nav_bttn
  {
    display: none;
  }

  .mobile_icon
  {
    display:block;
    padding-right: 10%;
  }

  .mobile_menu
  {
    background-color: #0423F6;
    width: 100%;
    position:fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .mobile_item
  {
    color:white;
    text-align: center;
    width: 100%;
    font-size: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .mobile_item:hover
  {
    cursor: pointer;
    border-bottom: white solid;
    border-width: 5px;
  }

}


*{
    border: 0;
    padding: 0;
    margin: 0;
}



.title_div
{
    text-align: left;
    padding-left: 75px;
    padding-top: 50px;
    padding-bottom: 50px;
}

.title_AboutMe
{
    font-family: 'Tiro Devanagari Sanskrit', serif;
    color: #060368;
    font-size: 40px;
    border-bottom: solid 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;

}

.first_AboutMe
{
    background-color:white;
    width:100%;
}

.second_AboutMe
{
    background-color: white;
    width: 100%;
    margin-right: 25px;
    display: flex;
    justify-content:space-evenly;
    padding-bottom: 150px;

}

.description
{
    background-color: #0423F6;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 50px;
    border-radius: 50px;
}

.desc
{
    color: white;
    font-size: large;
    text-align: left;
}

@media (max-width: 728px) 
{
    .second_AboutMe
    {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.project_div
{
    padding-bottom: 50px;
}

.project_title
{
    font-family: 'Tiro Devanagari Sanskrit', serif;
    color: white;
    font-size: 40px;
    border-bottom: solid 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.projects
{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    background-color:#0423F6;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.pokedex_img
{
    padding-top: 25px;
}

.tic_tac
{
    padding-top: 25px;
}

.project_desc
{
    list-style-position: inside;
    list-style: none;
    font-family: 'Tiro Devanagari Sanskrit', serif;
    text-align: left;
    padding: 20px;
}


.project
{
    background-color: white;
    width: 28%;
    text-align: center;
    padding-top: 20px;
}

.project_point
{
    padding-bottom: 30px;
    font-size: large;
}

.button_div
{
    display: flex;
    justify-content: center;
}


.buttons
{
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    padding-bottom: 20px;
}

#website_bttn
{
    padding-left: 50px;
    padding-right: 50px;
}

@media (max-width: 728px)
{
    .projects{
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 20px;
        gap: 20px;
    }

    .project{
        width: 80%;
    }
}

*{
    border: 0;
    padding: 0;
    margin: 0;
}

.skills_container{
    background-color: white;
}

.skills_title_div
{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-top: 50px;
    padding-left: 75px;
    padding-bottom: 50px;
    
}
.skills_title
{
    font-family: 'Tiro Devanagari Sanskrit', serif;
    font-size: 40px;
    border-bottom: 5px solid;
    
}


.skills_row_1
{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    padding-bottom: 20px;
}

.skills_card
{
    padding-left: 5px;
    width: 30%;
    box-shadow: 5px 5px 5px 5px whitesmoke;
}

.skills_icon
{
    color: black;
}



.skills_name
{
    font-size: 30px;
    text-align: left;
}

.skill_desc_div
{
    padding-top: 10px;
}

.skills_projects
{
    list-style-position: inside;
}

@media (max-width: 728px)
{
    .skills_row_1
    {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .skills_card
    {
        width: 80%;
    }
}
